import React from 'react';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap'
import { NavLink } from 'react-router-dom';
import { useUsuario } from '../contexts/AppContext';

const NavBar = () => {
    const rutas = [
        { nombre: "Clientes", path: "/Clientes" },
        //{ nombre: "Ventas", path: "/Ventas" },
        { nombre: "Stock", path: "/Stock" },
        { nombre: "Insumos", path: "/Insumos" },
        //{ nombre: "Empleados", path: "/Insumos" },
        //{ nombre: "Cuenta Corriente", path: "/Insumos" }
    ];
    const { usuario, logout } = useUsuario();

    const navRoutes = rutas.map((ruta) => {
        return (<NavLink key={ruta.path} className='nav-link' to={ruta.path} activeClassName="active">{ruta.nombre}</NavLink>)
    })
    return (<Navbar expand="lg">
        <Navbar.Brand href="#home">Gestión</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
                {usuario != null && (
                    navRoutes
                )
                }

            </Nav>
            <Nav className="ml-auto">

                {usuario != null ? (
                    <NavDropdown alignRight title={usuario} id="collasible-nav-dropdown">
                        <NavDropdown.Item href="#action/3.3">Ajustes</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#" onClick={logout}>Cerrar Sesión</NavDropdown.Item>
                    </NavDropdown>
                ) : (<NavLink className='nav-link' to='login' activeClassName="active">Iniciar Sesión</NavLink>)
                }

            </Nav>

        </Navbar.Collapse>
    </Navbar>)
}

export default NavBar;